.welcome-message{
  width:75%;
}

.greebin-trademark{
    width:100%;
    height:auto !important;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    color: #808080;
    font-weight: 600;
    margin-bottom:20px;
}

.greebin-trademark .greenbin{
  font-size:40px;
  line-height:1;
  margin:0;
  padding:0;
}

.suggestive-message{
  width:100%;
  text-align:center;
}

.suggestive-message span{
  line-height:20px;
  color: #808080;
}

.image-wrapper {
  width: 100%; 
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

  .image-wrapper img{
    width: 60% ;
    height: auto; 
  }

  .form{
    width:100%;
    display:flex;
    flex-direction:column;
    align-items:center;
  }

  .sign-in-form{
    width:75%;
  }

  .form-inputs{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;

  }

  .login-button{
    width:100%;
    display:flex;
    justify-content:center;
  }